import React from 'react' 
import { NavLink } from 'react-router-dom';  

import logo from '../assets/images/logo/logo-3.png'
import sprite from '../assets/images/sprite.svg'

function Header(props) { 

    const list = props.linksList

    return ( 
        <header className="header header_theme_3">
  <div className="header__top">
    <div className="container header__container">
        <NavLink className="logo header__logo" to="/">
            <img className="logo__icon" src={logo} alt='Сауны' /> 
            <span className="logo__title">Строительство саун в СПб</span>
        </NavLink>
      <div className="header__left">
        <div className="header__phone">
            <a className="phone phone_theme_3" href="tel:+78007008000">
            {/* Если указана иконка*/}<svg className="icon icon_phone-call phone__icon" role="img">
              <use href={sprite + "#phone-call"} />
            </svg>
            {/*Если потребуются свои элементы, то можно указать поcле +phone()*/}<span className="phone__number">8 800 700 8000</span></a>
            <a className="phone phone_theme_3" href="tel:+78007008000">
            {/* Если указана иконка*/}<svg className="icon icon_phone-call phone__icon" role="img">
                <use href={sprite + "#phone-call"} />
            </svg>
            {/*Если потребуются свои элементы, то можно указать поcле +phone()*/}<span className="phone__number">8 800 700 8000</span></a></div>
        <div className="header__btn">
          <button className="button button_theme_12" type="button">
            <svg className="icon icon_phone-call button__icon" role="img">
                <use href={sprite + "#phone-call"} />
            </svg>
            <span className="button__text">Заказать звонок</span>
          </button></div>
        <div className="header__busket"><svg className="icon icon_shopping-cart" role="img">
            <use href={sprite + "#shopping-cart"} /> 
          </svg><span className="header__busket-name">корзина</span><span className="header__busket-count">1</span></div>
        <div className="header__mobile-menu-trigger">
          <div className="hamburger-btn js-btn-menu" role="button">
            <div className="hamburger hamburger--slider">
              <div className="hamburger-box">
                <div className="hamburger-inner" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div className="header__menu sura-header">
    <div className="container">
      <nav className="main-menu main-menu_theme_3">
        <ul className="main-menu__list">
        {
            list.length > 0 &&
            list.map( (item, index) => {
                return (
                    <li key={index} className="main-menu__item"> 
                        <NavLink className="main-menu__link" to={`/${item.slug}`}>
                            {item.title} 
                        </NavLink>
                            {
                                item['list-service-menu'] && item['list-service-menu'].length > 0 &&
                                <ul>
                                    {
                                        item['list-service-menu'].map( (innerItem) => {
                                            return(
                                                <li key={innerItem.id} className='nav-item'>
                                                    <NavLink exact to={`/${item.slug}/${innerItem.slug}`} className="main-menu__link">
                                                        {innerItem.title}
                                                    </NavLink>
                                                </li>
                                            )
                                        })
                                    } 
                                </ul>
                            }
                            {
                                item['list-brands-menu'] && item['list-brands-menu'].length > 0 &&
                                <ul className='second-list'>
                                    {
                                        item['list-brands-menu'].map( (innerItem) => {
                                            return(
                                                <li key={innerItem.id} className="main-menu__item">
                                                    <NavLink exact to={`/${item.slug}/${innerItem.slug}`} className="main-menu__link" >
                                                        {innerItem.title}
                                                    </NavLink>
                                                </li>
                                            )
                                        })
                                    } 
                                </ul>
                            }
                            
                    </li>
                )
            })
        }
          
        </ul>
      </nav>
    </div>
  </div>
  <div className="header__menu-mobile">
    <div className="mobile-menu mobile-menu_theme_3">
      <div className="container mobile-menu__container">
        <nav className="main-menu main-menu_theme_3 mobile-menu__menu">
          <ul className="main-menu__list">
            <li className="main-menu__item"><a className="main-menu__link" href="#catalog">Каталог</a></li>
            <li className="main-menu__item"><a className="main-menu__link" href="#company">O компании</a></li>
            <li className="main-menu__item"><a className="main-menu__link" href="#gallary">Фотогалерея</a></li>
            <li className="main-menu__item"><a className="main-menu__link" href="#saun">Сауны</a></li>
            <li className="main-menu__item"><a className="main-menu__link" href="#xamam">Хамамы</a></li>
            <li className="main-menu__item"><a className="main-menu__link" href="#contacts">Контакты</a></li>
          </ul>
        </nav>
        <div className="mobile-menu__time"><svg className="icon icon_clock" role="img">
            <use xlinkHref="assets/images/sprite.svg#clock" />
          </svg><span>Пн-Пт с 10.00 до 18.00, Сб-Вс выходной</span></div>
        <div className="mobile-menu__phone"><a className="phone phone_theme_3" href="tel:+78007008000">
            {/* Если указана иконка*/}<svg className="icon icon_phone-call phone__icon" role="img">
              <use xlinkHref="assets/images/sprite.svg#phone-call" />
            </svg>
            {/*Если потребуются свои элементы, то можно указать поcле +phone()*/}<span className="phone__number">8 800 700 8000</span></a><a className="phone phone_theme_3" href="tel:+78007008000">
            {/* Если указана иконка*/}<svg className="icon icon_phone-call phone__icon" role="img">
              <use xlinkHref="assets/images/sprite.svg#phone-call" />
            </svg>
            {/*Если потребуются свои элементы, то можно указать поcле +phone()*/}<span className="phone__number">8 800 700 8000</span></a></div>
        <div className="mobile-menu__btn">
          <button className="button button_theme_12" type="button">
            <svg className="icon icon_phone-call button__icon" role="img">
              <use xlinkHref="assets/images/sprite.svg#phone-call" />
            </svg>
            <span className="button__text">Заказать звонок</span>
          </button></div>
      </div>
    </div>
  </div>
</header>

        
        /* <header className='sura-header'>
            <div className='container'>
                <nav className="navbar-expand-sm">
                    
                    <div className=" navbar-collapse justify-content-between" >
                        <ul className="navbar-nav ">
                            <li className="nav-item">
                                <NavLink exact className=" sura-header-link" to="/">Главная</NavLink>
                            </li>
                            {
                                list.length > 0 &&
                                list.map( (item, index) => {
                                    return (
                                        <li key={index} className="nav-item"> 
                                            <NavLink className=" sura-header-link" to={`/${item.slug}`}>
                                                {item.title} 
                                            </NavLink>
                                                {
                                                    item['list-service-menu'] && item['list-service-menu'].length > 0 &&
                                                    <ul>
                                                        {
                                                            item['list-service-menu'].map( (innerItem) => {
                                                                return(
                                                                    <li key={innerItem.id} className='nav-item'>
                                                                        <NavLink exact to={`/${item.slug}/${innerItem.slug}`}>
                                                                            {innerItem.title}
                                                                        </NavLink>
                                                                    </li>
                                                                )
                                                            })
                                                        } 
                                                    </ul>
                                                }
                                                {
                                                    item['list-brands-menu'] && item['list-brands-menu'].length > 0 &&
                                                    <ul className='second-list'>
                                                        {
                                                            item['list-brands-menu'].map( (innerItem) => {
                                                                return(
                                                                    <li key={innerItem.id} className='nav-item'>
                                                                        <NavLink exact to={`/${item.slug}/${innerItem.slug}`}>
                                                                            {innerItem.title}
                                                                        </NavLink>
                                                                    </li>
                                                                )
                                                            })
                                                        } 
                                                    </ul>
                                                }
                                                
                                        </li>
                                    )
                                })
                            }
                           
                        </ul>
                        {/* <div className="search-wrapper">
                            <div className="search-button"></div>
                            <nav className="form-search">
                                <form action="/search" method="get">
                                    <input type="text" name="search" 
                                        // onBlur="this.value=this.value==''?'Поиск по сайту':this.value" 
                                        // onFocus="this.value=this.value=='Поиск по сайту'?'':this.value;" 
                                        // value="Поиск по сайту"
                                        defaultValue='Поиск по сайту'
                                        />
                                    <input type="submit" value="" />
                                </form>
                            </nav>
                        </div>  
                    </div>
                </nav>
               
            </div> 
        </header>   */ 

    )

     
}

export default Header
